*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  font-family: "Open Sans", "Arial", sans-serif;
  box-sizing: border-box;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  max-width: 100vw;
  min-height: 100vh;
  min-width: $mobile-width;
  will-change: background-color;
  background: radial-gradient(circle, rgba(2,0,36,1) 0%, rgba(117,117,167,0.34217436974789917) 0%, rgba(0,212,255,1) 100%);
}

a {
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
.form-radio input[type="radio"],
.form-checkbox input[type="checkbox"],
input[type="file"] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.lock {
  overflow: hidden;
  margin-right: 16px;
}

.shim {
  position: fixed;
  bottom: 0;
  left: 0;
  top: 0;
  right: 0;
  overflow: auto;
  background: rgba(0, 0, 0, 0.5);
}
