.demos__list {
  display: flex;
  flex-wrap: wrap;
}

.demos__title {
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

.demos__item {
  width: 30%;
  margin-bottom: 20px;
  text-align: center;

  padding-bottom: 10px;

  a {
    margin: 20px auto 0;
    text-align: center;
    display: inline-block;
    text-decoration: none;
    transition: all 0.3s ease-in-out;
    text-transform: uppercase;

    &:hover {
      color: tomato;
    }
  }

  &:not(:nth-of-type(3n + 3)) {
    margin-right: 5%;
  }
}

.demos__wrapper {
  position: relative;
  overflow: hidden;
  border: 5px solid rgba(0, 0, 0, 0.1);

  .demos__description {
    text-align: left;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: auto;
    top: 0;
    left: 0;
    padding: 10px;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    color: $white;

    font-size: 12px;

    transform: translateY(100%);
    transition: all 0.3s ease-in-out;
  }

  img {
    width: 100%;
  }

  ul {
    @include list-reset;

    li {
      padding: 5px 0;

      &::before {
        content: "-  "
      }
    }
  }

  button {
    align-self: center;
    margin-bottom: 10px;
    text-decoration: none;
    border: none;
    border-bottom: 1px solid $white;
    background: none;
    cursor: pointer;
    font-family: "Open Sans", "Arial", sans-serif;
    color: $white;
    text-transform: uppercase;
    outline: none;

    &:hover {
      text-shadow: 0.1px 0.1px $white;
    }
  }

  &:hover {
    .demos__description {
      transform: translateY(0);
    }
  }
}



