.modal {
  display: none;
  position: absolute;
  top: 20%;
  left: 50%;
  width: 800px;

  transform: translate(-50%, -20%) ;
  background-color: rgba(0, 0, 0, 1);

  border: 5px solid $white;
  padding: 10px;
  color: $white;
  z-index: 100;

  &::after {
    z-index: -1;
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    background-size: cover;
    opacity: 0.4;
  }

  &--pink {
    &::after {
      background:  url('./../img/pink.png') no-repeat 50% 50%;
    }
  }



  &--show {
    display: block;
  }
}
